<template>
  <div class="content">
    <h1>üéôÔ∏è Welcome to ArtistTopSongs!!! üéôÔ∏è</h1>
    <label>Insert the name of the artist that you want to search for</label>
    <br>
  </div>
  <form @submit.prevent="getResults">
    <div class="column is-half is-offset-one-quarter">
       <input class="input" type="text" v-model="inputValue">
    </div>
  </form>
  <div class="content">
    <div v-if="results.length">
      <li v-for="result in results" :key="result">
        {{ result }}
      </li>
    </div>
    <div v-if="error">
      <h2>{{ error }}</h2>
    </div>
  </div>

</template>

<script>
import { ref } from 'vue';
const qs = require("qs");

export default {
  name: 'App',
  setup() {
    const inputValue = ref("");
    const results = ref([]);
    const error = ref(null);

    const getResults = async () => {
      if (inputValue.value === "") {
        return;
      }
      results.value = [];
      error.value = null;
      const params = {
        "artist": inputValue.value,
      };
      try {
        let data = await fetch("http://localhost:3000/api/?" + qs.stringify(params));
        if (!data.ok) {
          throw Error("couldn't fetch the requested data");
        }
        results.value = await data.json();
      }
      catch(err) {
        error.value = err.message;
        console.log(error.value);
      }
      inputValue.value = "";
    };

    return { inputValue, getResults, results, error };
  },
}
</script>

